<template>
  <div class="container">
    <div class="form">
      <el-form label-width="180px" :model="queryData" :rules="rules" :inline="true" ref="queryForm">
        <el-form-item label="机构名称" prop="org">
          <el-select v-model="queryData.org" placeholder="请选择机构名称">
            <el-option
              value="00000"
              label="总公司">
            </el-option>
            <el-option
              v-for="item in orgList"
              :key="item.code"
              :value="item.code"
              :label="item.name">
              <span>&nbsp;&nbsp;&nbsp;{{ item.name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button plain size="small" type="primary" @click="query">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="data" :hidden.sync="dataHidden">
      <div class="table">
        <base-table
          :columns="columns"
          :data="data"
          rowKey="id">
        </base-table>
      </div>
    </div>
  </div>
</template>

<script>
const BaseTable = () => import('@/components/BaseTable')
export default {
  name: 'index',
  components: {BaseTable},
  data () {
    return {
      queryData: {
        org: ''
      },
      rules: {
        org: [{required: true, trigger: 'change', message: '机构名称不能为空'}]
      },
      dataHidden: true,
      orgList: [
        {code: '10000', name: '北京分公司'},
        {code: '20000', name: '上海分公司'},
        {code: '30000', name: '江苏省分公司'},
        {code: '40000', name: '山东省分公司'},
        {code: '50000', name: '山西省分公司'}
      ],
      columns: [
        {prop: 'org', label: '机构名称', width: '120'},
        {prop: 'channelDown', label: '自主渠道系数下限', width: '', isEdit: true, type: 'input'},
        {prop: 'channelUp', label: '自主渠道系数上限', width: '', isEdit: true, type: 'input'},
        {prop: 'protectDown', label: '自主核保系数下限', width: '', isEdit: true, type: 'input'},
        {prop: 'protectUp', label: '自主核保系数上限', width: '', isEdit: true, type: 'input'},
        {prop: 'serviceDown', label: '手续费率下限', width: '', isEdit: true, type: 'input'},
        {prop: 'serviceUp', label: '手续费率上限', width: '', isEdit: true, type: 'input'}
      ],
      data: [],
      secondData: [
        {id: '1', org: '北京分公司', channelDown: '0.75', channelUp: '', protectDown: '0.85', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '2', org: '上海分公司', channelDown: '0.75', channelUp: '', protectDown: '0.85', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '3', org: '江苏省分公司', channelDown: '0.75', channelUp: '', protectDown: '0.85', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '4', org: '山东省分公司', channelDown: '0.75', channelUp: '', protectDown: '0.75', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '5', org: '山西省分公司', channelDown: '0.75', channelUp: '', protectDown: '0.80', protectUp: '', serviceDown: '', serviceUp: ''}
      ],
      thirdData: [
        {id: '1', org: '山西省分公司太原支公司', channelDown: '0.75', channelUp: '', protectDown: '0.80', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '2', org: '山西省分公司大同支公司', channelDown: '0.75', channelUp: '', protectDown: '0.80', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '3', org: '山西省分公司阳泉支公司', channelDown: '0.75', channelUp: '', protectDown: '0.80', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '4', org: '山西省分公司长治支公司', channelDown: '0.75', channelUp: '', protectDown: '0.80', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '5', org: '山西省分公司晋城支公司', channelDown: '0.75', channelUp: '', protectDown: '0.80', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '6', org: '山西省分公司朔州支公司', channelDown: '0.75', channelUp: '', protectDown: '0.80', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '7', org: '山西省分公司晋中支公司', channelDown: '0.75', channelUp: '', protectDown: '0.80', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '8', org: '山西省分公司运城支公司', channelDown: '0.75', channelUp: '', protectDown: '0.80', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '9', org: '山西省分公司忻州支公司', channelDown: '0.75', channelUp: '', protectDown: '0.80', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '10', org: '山西省分公司临汾支公司', channelDown: '0.75', channelUp: '', protectDown: '0.80', protectUp: '', serviceDown: '', serviceUp: ''},
        {id: '11', org: '山西省分公司吕梁支公司', channelDown: '0.75', channelUp: '', protectDown: '0.80', protectUp: '', serviceDown: '', serviceUp: ''}
      ]
    }
  },
  methods: {
    query () {
      this.$refs['queryForm'].validate(valid => {
        if (valid) {
          if (this.queryData.org === '') {
            this.dataHidden = true
            this.data = []
          } else if (this.queryData.org === '00000') {
            this.dataHidden = false
            this.data = this.secondData
          } else if (this.queryData.org === '50000') {
            this.dataHidden = false
            this.data = this.thirdData
          } else {
            this.dataHidden = false
            this.data = []
          }
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



